import * as types from '../src/types/types';
import * as actions from '../src/action/actions';

import configureMockStore from 'redux-mock-store';
import thunk from 'redux-thunk';

import fetchMock from 'fetch-mock';
import expect from 'expect'; // You can use any testing library
import {fetchUpcomingMovies} from '../src/action';

const middlewares = [thunk];
const mockStore = configureMockStore(middlewares);

describe('async actions', () => {
  afterEach(() => {
    fetchMock.restore();
  });

  it('creates actions when fetching has been done', () => {
    fetchMock.getOnce(
      'https://api.themoviedb.org/3/movie/upcoming?api_key=b3a72b722e11795c7179cc928980a9e2',
      {
        headers: {'content-type': 'application/json'},
      },
    );

    const expectedActions = [
      {type: types.REQUEST_UPCOMING_MOVIES_ACTION},
      {
        type: types.REQUEST_UPCOMING_MOVIES_SUCCESS_ACTION,
        data: [],
      },
    ];
    const store = mockStore({todos: []});

    return store.dispatch(fetchUpcomingMovies()).then(() => {
      // return of async actions
      expect(store.getActions()).toEqual(expectedActions);
    });
  });
});
